/* ==============================================  Colors */

:root {
  --mainClr: hsl(0, 0%, 10%);
  --softClr: hsl(0, 0%, 85%);
  --bgClr: hsl(0, 0%, 98%);
}

body {
  background-color: hsl(0, 0%, 12%);
}

/* ==============================================  Wrappers */

.App {
  text-align: center;
  font-family: monospace;
  color: var(--mainClr);
  background-color: var(--bgClr);
  padding: 0.25rem;
  border-radius: 0.25rem;
}

@media (max-width: 400px) {
  body {
    display: block;
    margin: 0;
  }
  .App {
    border-radius: 0;
  }
}

@media (min-width: 420px) {
  body {
    display: flex;
    justify-content: center;
  }
  .App {
    width: 400px;
  }
}

/* ==============================================  Buttons */

button {
  display: flex;
  padding: 0.25rem;
  margin-bottom: 0.5rem;
  border-width: 4px;
  border-radius: 1.75rem;
  color: var(--mainClr);
  background-color: var(--bgClr);
}

/* ==============================================  Top Bar */

#topBar {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  min-height: calc(4.8rem + 2px);
}

#resetButton svg,
#darkmodeSwitch svg {
  max-width: 1.5rem;
  max-height: 1.5rem;
}

#uiContainer {
  padding: 0 0.25rem 0.25rem 0;
  text-align: left;
  border-right: 2px solid var(--softClr);
  border-bottom: 2px solid var(--softClr);
  border-bottom-right-radius: 0.25rem;
  line-height: 1.1rem;
  min-width: 8rem;
}

.uiItem {
  min-width: 100%;
  display: flex;
  align-items: center;
}

.uiItem svg {
  max-width: 1.4rem;
  max-height: 1.4rem;
  margin: 0.2rem 0.4rem 0.2rem 0;
}

#hungerIndicator,
#lonelinessIndicator,
#dirtinessIndicator {
  background-color: var(--mainClr);
  height: 0.7rem;
}

/* ============================================== Pet */

.petContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 30vh;
}

#pet {
  margin-top: 10vh;
  padding-bottom: 5vh;
  user-select: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#petShadow {
  margin-bottom: 5vh;
  border-radius: 50%;
  background-color: var(--softClr);
}

.hatching {
  animation: hatch 2s ease-in-out infinite;
}
.alive {
  animation: wobble 3s ease-in-out infinite;
}
.sick {
  animation: wobble 5s ease-in-out infinite;
}
.dead {
  animation: none;
}
.fed {
  animation: fed 1.5s ease-in-out forwards;
}
.petted {
  animation: petted 1.5s linear forwards;
}
.cleaned {
  animation: cleaned 1.5s ease-in-out forwards;
}

/* ============================================== Actions Menu */

.actionsMenu {
  display: flex;
  justify-content: center;
}

.actionsMenu button:not(:last-of-type) {
  margin-right: 0.75rem;
}

.actionsMenu svg,
.actionsMenu svg {
  max-width: 2rem;
  max-height: 2rem;
}

/* ============================================== Animations */

@keyframes fed {
  0% {
    transform: scale(1, 1);
  }
  20% {
    transform: scale(1, 0.9);
  }
  40% {
    transform: scale(0.9, 1);
  }
  60% {
    transform: scale(1, 0.9);
  }
  80% {
    transform: scale(0.9, 1);
  }
  100% {
    transform: scale(1, 0.9);
  }
}

@keyframes petted {
  0% {
    transform: translate(0) rotateX(0);
  }
  20% {
    transform: translate(0, -0.2rem) rotateZ(2.5deg);
  }
  40% {
    transform: translate(-0.1rem, -0.4rem) rotateZ(5deg);
  }
  60% {
    transform: translate(0, -0.2rem) rotateZ(-2.5deg);
  }
  80% {
    transform: translate(0.1rem, -0.4rem) rotateZ(-5deg);
  }
  100% {
    transform: translate(0) rotateX(0);
  }
}

@keyframes cleaned {
  0% {
    transform: rotateY(0);
  }
  30% {
    transform: rotateY(30deg);
  }
  40% {
    transform: rotateY(-30deg);
  }
  50% {
    transform: rotateY(30deg);
  }
  60% {
    transform: rotateY(-30deg);
  }
  70% {
    transform: rotateY(30deg);
  }
  100% {
    transform: rotateY(0);
  }
}

@keyframes wobble {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.05, 0.9) translateY(7px);
  }
  100% {
    transform: scale(1, 1);
  }
}

@keyframes hatch {
  0% {
    transform: translateX(0);
  }
  10% {
    transform: translateX(-0.25rem);
  }
  20% {
    transform: translateX(0.25rem);
  }
  30% {
    transform: translateX(0);
  }
  40% {
    transform: translateX(0.25rem);
  }
  50% {
    transform: translateX(-0.25rem);
  }
  60% {
    transform: translateX(0.25rem);
  }
  70% {
    transform: translateX(-0.25rem);
  }
  80% {
    transform: translateX(0.25rem);
  }
  90% {
    transform: translateX(-0.25rem);
  }
  100% {
    transform: translateX(0.25rem);
  }
}
